import React, { useState } from 'react';
import { Nav, Navbar, Container } from 'react-bootstrap';
import { Link } from 'react-router-dom';

import './styles/NavigationMenu.module.css'; // Ensure this file exists and contains your custom styles

export default function NavigationMenu() {
  const [isNavOpen, setIsNavOpen] = useState<boolean>(false);

  const toggleNav = () => setIsNavOpen(!isNavOpen);

  return (
    <Navbar bg="dark" variant="dark" expand="lg" className="rounded rounded-4 navbar-custom">
      <Container className='p-3'>
        <Navbar.Brand as={Link} to="/" className="navbar-logo">Todo App</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" onClick={toggleNav} />
        <Navbar.Collapse id="basic-navbar-nav" className={`collapse ${isNavOpen ? 'show' : ''}`}>
          <Nav className="ml-auto flex-grow-1 justify-content-end">
            <Nav.Item>
              <Link to="/" className="nav-link">Home</Link>
            </Nav.Item>
            <Nav.Item>
              <Link to="/about" className="nav-link">About</Link>
            </Nav.Item>
            <Nav.Item>
              <Link to="/contact" className="nav-link">Contact</Link>
            </Nav.Item>
            <Nav.Item>
              <Link to="/register" className="nav-link">Register</Link>
            </Nav.Item>
            <Nav.Item>
              <Link to='/user-list' className='nav-link'>User List</Link>
            </Nav.Item>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}
